	<text style="font-size: 12px;">
		隐私政策    
		

		版本：
		
		发布日期：2021年03月17日    
		

		感谢您选择使用我们的服务！    
		
		"我们"是衢州市王德裕堂国药有限公司，注册地位于中国衢州。
		
		"您"是指“王德裕堂”小程序的注册用户。    
		
		"我们的服务"是指我们通过“王德裕堂”小程序或我们发布的其它渠道（以下统称“我们的平台”）向您提供的互联网平台信息技术服务。    
		
		保护您的隐私对我们至关重要，我们会尽全力保护您的个人信息安全可靠。我们致力于维持您对我们的信任，并恪守以下原则，保护您的个人信息：权责一致原则、目的明确原则、选择同意原则、最少够用原则、确保安全原则、主体参与原则、公开透明原则等。同时，我们承诺，我们将按业界成熟的安全标准，采取相应的安全保护措施来保护您的个人信息。请您仔细阅读本《隐私政策》，该政策解释我们如何收集、使用以及披露我们在服务中收到的信息。您使用服务即表示同意我们的《隐私政策》，即同意我们按照本《隐私政策》收集、使用、处理和存储您的相关信息。如果您不同意本《隐私政策》，请您立即注销账户以终止使用我们的产品和服务。     
		
		目录    
		
		我们的《隐私政策》解释了以下几个方面的问题：    
		
		1、我们如何收集和使用您的个人信息     
		
		2、我们如何使用技术     
		
		3、我们如何存储和保护您的个人信息     
		
		4、您的权利     
		
		5、我们如何保护未成年人的信息     
		
		6、本政策如何更新     
		
		7、如何联系我们    
		
	
		隐私政策具体内容     
		
		1.我们如何收集和使用您的个人信息    
		
		我们收集您的信息的主要目的是提供及改善我们的服务，便捷您对我们的服务的使用。我们会按照如下方式收集您在使用服务时主动提供的、以及通过自动化手段收集您在使用功能或接受服务过程中产生的信息。    
		
		1.注册和登录    
		
		当您注册、登录您的“王德裕堂”小程序账户使用我们的服务时，我们将收集您的手机号码用于确认您的身份。在您使用我们的服务时，我们也会根据服务内容的需要，向您收集必要的信息，并在收集时向您做出明确的提示。    
		
		2.资讯浏览    
		
		您可浏览的内容包括产品信息、专家信息、科室信息、评论、健康类文章等等。
		
		3.搜索功能    
		
		当您使用”王德裕堂”小程序提供的搜索功能时，我们会收集您查询的关键字信息以及您在使用我们的服务时所阅览或要求的其他信息和内容详情。为了给您带来更便捷的搜索服务并不断完善我们的服务，我们可能会使用浏览器网络存储机制和应用数据缓存，收集您设备上的信息并进行本地存储。关键词信息通常无法单独识别您的个人身份，不属于您的个人信息，不在本政策的限制范围内。只有当您的搜索关键词信息与您的其他信息有联结并可识别您的个人身份时，则在结合使用期间，我们会将您的搜索关键词信息作为您的个人信息，与您的搜索历史记录一同按照本政策对其进行处理与保护。如果您不提供上述信息，将会影响您使用此功能。    
		
		5.购物功能    
		
		您可在”王德裕堂”小程序在线购买相应健康商城商品。在您使用该功能的过程中可能会需要进行支付，在支付过程中，我们可能会收集您的第三方支付帐号(支付宝帐号、微信支付帐号及银行卡信息)用于付款验证，以及收集您的收货地址信息，包括收货人、地址、手机号码。如果您不提供上述信息，将会影响您使用此功能。       
		
		6.健康档案    
		
		当您在使用健康档案功能时，我们需要您向我们授权拍摄并读取相册、读取外部存储及写入外部存储的相关权限，用于完善用户健康档案相关资料的传输。同时，在此过程中，我们将可能收集您得处方、科室、医院、体检机构、检测项目、体检/就诊时间信息，用于完善用户的就诊/体检记录。如果您不提供上述信息，将会影响您使用此功能。    
		
		7.安全运行    
		
		1.7.1 安全保障功能我们致力于为您提供安全、可信的产品与使用环境，提供优质而可靠的服务是我们的核心目标。读、写入外部存储，唯一设备标识符为实现安全运行所收集的必要权限和信息。如果您不提供上述信息，将会影响您使用此功能。    
		
		1.7.2 设备信息与日志信息    
		
		a.为了保障软件与服务的安全运行，我们会收集您的硬件型号、android_id、操作系统版本号、国际移动设备识别码（SIM卡序列号）、SIM卡运营商、唯一设备标识符（IMSI、IMEI、）、网络设备硬件地址、IP 地址、Mac地址、WLAN接入点、蓝牙、基站、软件版本号、网络接入方式、类型、状态、网络质量数据、操作、使用、服务日志。    
		
		b.为了预防恶意程序及安全运营所必需，我们会收集安装的应用信息或正在运行的进程信息、应用程序的总体运行、使用情况与频率、应用崩溃情况、总体安装使用情况、性能数据、应用来源。    
		
		c.我们可能使用您的账户信息、设备信息、服务日志信息以及我们关联公司、合作方在获得您授权或依法可以共享的信息，用于判断账户安全、进行身份验证、检测及防范安全事件。    
		
		e.如果您不提供上述信息，将会影响您使用此功能。    
		
		f. 当您需要复制粘贴信息至”王德裕堂”小程序时，需要申请您的剪切板权限，获得您粘贴的信息。   
		
		当我们要将信息用于本政策未载明的其它用途时，会事先征求您的同意。
		
		当我们要将基于特定目的收集而来的信息用于其他目的时，会事先征求您的同意。    
		
		但以下情形中除外：    
		
		a) 与个人信息控制者履行法律法规规定的义务相关的；    
		
		b) 与国家安全、国防安全直接相关的；    
		
		c) 与公共安全、公共卫生、重大公共利益直接相关的；    
		
		d) 与刑事侦查、起诉、审判和判决执行等直接相关的；    
		
		e) 出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人授权同意的；    
		
		f) 所涉及的个人信息是个人信息主体自行向社会公众公开的；    
		
		g) 根据个人信息主体要求签订和履行合同所必需的；    
		
		h) 从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道；    
		
		i) 维护所提供产品或服务的安全稳定运行所必需的，如发现、处置产品或服务的故障。    
		

		2.我们如何使用Cookie等技术    
		
		为确保网站正常运转，我们会在您的计算机或移动设备上存储名为Cookie的小数据文件。Cookie 通常包含标识符、站点名称以及一些号码和字符。借助于Cookie，网站能够存储您的登录状态等数据，提升服务/产品质量及优化用户体验。我们不会将 Cookie用于本政策所述目的之外的任何用途。    
		

		4.我们如何存储和保护您的个人信息    
		
		4.1 存储地点    
		
		我们依照法律法规的规定，将在境内运营过程中收集和产生的您的个人信息存储于中华人民共和国境内。目前，我们不会将上述信息传输至境外，如果我们向境外传输，我们将遵循相关国家规定或者征求您的同意。    
		
		4.2存储期限    
		
		我们仅在为提供”王德裕堂”小程序及服务之目的所必须的期间内保存您的个人信息。超出必要期限后，我们将对您的个人信息进行删除或匿名化处理，但法律法规另有规定的除外。    
		
		4.3 技术措施与数据保护措施    
		
		我们已使用符合业界标准的安全防护措施保护您提供的个人信息，防止数据遭到未经授权访问、公开披露、使用、修改、损坏或丢失。我们会采取一切合理可行的措施，保护您的个人信息。例如，在您的浏览器与“服务”之间交换数据（如信用卡信息）时受 SSL 加密保护；我们同时对”王德裕堂”小程序提供 https 安全浏览方式；我们会使用加密技术确保数据的保密性；我们会使用受信赖的保护机制防止数据遭到恶意攻击；我们会部署访问控制机制，确保只有授权人员才可访问个人信息；以及我们会举办安全和隐私保护培训课程，加强员工对于保护个人信息重要性的认识。    
		
		4.4 安全认证     
		
		我们已经取得了以下认证：国家信息安全等级保护（三级）的评测和备案    
		
		4.5 安全事件处理    
		
		在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。 同时，我们还将按照监管部门要求，主动上报个人信息安全事件的处置情况。    
		

		5.您的权利    
		
		按照中国相关的法律、法规、标准，以及其他国家、地区的通行做法，我们保障您对自己的个人信息行使以下权利：     
		
		5.1访问您的个人信息     
		
		您有权访问您的个人信息，法律法规规定的例外情况除外。如果您想行使数据访问权，可以通过以下方式自行访问：     
		
		账户信息——如果您希望访问或编辑 您的账户中的头像、昵称、健康号、手机号等，您可以通过访问”王德裕堂”小程序—>我的—>个人信息进行查看。     
		
		对于您在使用我们的产品或服务过程中产生的其他个人信息，只要我们不需要过多投入，我们会向您提供。如果您想行使数据访问权，可通过本政策第8条提供的联系方式与我们联系。    
		
		5.2更正您的个人信息     
		
		当您发现我们处理的关于您的个人信息有错误时，您有权要求我们做出更正。您可以通过5.1访问您的个人信息”中罗列的方式提出更正申请。     
		
		如果您无法通过上述链接更正这些个人信息，您可以通过本政策第8条提供的联系方式与我们联系。     
		
		5.3删除您的个人信息     
		
		在以下情形中，您可以向我们提出删除个人信息的请求：     
		
		1、如果我们处理个人信息的行为违反法律法规；     
		
		2、如果我们收集、使用您的个人信息，却未征得您的同意；     
		
		3、如果我们处理个人信息的行为违反了与您的约定；     
		
		4、如果您不再使用我们的产品或服务，或您注销了账号；     
		
		5、如果我们不再为您提供产品或服务。     
		
		若我们决定响应您的删除请求，我们还将同时通知从我们获得您的个人信息的实体，要求其及时删除，除非法律法规另有规定，或这些实体获得您的独立授权。     
		
		当您从我们的服务中删除信息后，我们可能不会立即备份系统中删除相应的信息，但会在备份更新时删除这些信息。如果您想行使数据删除权，可通过本政策第8条提供的联系方式与我们联系。    
		
		5.4个人信息主体注销账户     
		
		您随时可注销此前注册的账户，可通过访问”王德裕堂”小程序—>我的—>设置—>账户信息—>注销账户信息。    
		
		在注销账户之后，我们将停止为您提供产品或服务，并依据您的要求，删除您的个人信息，法律法规另有规定的除外。     
		
		5.6约束信息系统自动决策     
		
		在某些业务功能中，我们可能仅依据信息系统、算法等在内的非人工自动决策机制做出决定。如果这些决定显著影响您的合法权益，您有权要求我们做出解释，我们也将提供适当的救济方式。     
		
		5.7响应您的上述请求     
		
		为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。我们将在十五天内做出答复。    
		
		对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份磁带上存放的信息）的请求，我们可能会予以拒绝。
		
		在以下情形中，我们将无法响应您的请求：     
		
		1、与个人信息控制者履行法律法规规定的义务相关的；     
		
		2、与国家安全、国防安全直接相关的；     
		
		3、与公共安全、公共卫生、重大公共利益直接相关的；     
		
		4、与犯罪侦查、起诉、审判和执行判决等直接相关的；     
		
		5、个人信息控制者有充分证据表明个人信息主体存在主观恶意或滥用权利的；     
		
		6、出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；     
		
		7、响应个人信息主体的请求将导致个人信息主体或其他个人、组织的合法权益受到严重损害的；     
		
		8、涉及商业秘密的。    
		
		9、若我们决定不响应您的请求，我们将向您告知该决定的理由，您也可通过本政策第8条提供的联系方式进行投诉。    
		

		6.本政策如何更新    
		
		我们可能会不时对本《隐私政策》进行修订，当本《隐私政策》发生修订时，我们会在版本更新后以推送通知、弹窗等形式向您展示变更后的内容。请您注意，在《隐私协议》变更后，我们将无法保证按照其变更前的内容收集、使用、处理和存储您的个人信息。只有在您确认修订后的《隐私政策》后，我们才会按照修订后的《隐私政策》收集、使用、处理和存储您的个人信息。因此，如果您不同意修订后的《隐私政策》，请您停止使用我们的服务。您继续使用我们服务的行为也同样会被视为您同意并接受我们修订后的《隐私政策》。    
		
		未经您明确同意，我们不会削减您按照本隐私政策所应享有的权利。我们会在本页面上发布对本政策所做的任何变更。     
		
		对于重大变更，我们还会提供更为显著的通知（包括对于某些服务， 我们会通过电子邮件发送通知，说明隐私政策的具体变更内容）。
		
		本政策所指的重大变更包括：     
		
		1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；   
		
		2、我们在所有权结构、组织架构等方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；     
		
		3、个人信息共享、转让或公开披露的主要对象发生变化；     
		
		4、您参与个人信息处理方面的权利及其行使方式发生重大变化；     
		
		5、我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生变化时；     
		
		6、个人信息安全影响评估报告表明存在高风险时。     
		
		我们还会将本政策的旧版本存档，供您查阅。    
		

		8.如何联系我们    
		
		我们已经设立了个人信息保护负责人员，如果您对本隐私政策有任何疑问、意见、投诉或建议，通过以下方式与我们联系：    
		
		电话：400-996-0113    
		
		如果您对我们的回复不满意，特别是我们的个人信息处理行为损害了您的合法权益，您还可以向网信、电信、公安及工商等监管部门进行投诉或举报。
